<template>
<div class="fadeoutframe main" id="main">
  <div id="firstscreen" class="firstscreen">
    <div id="fpbackpic" class="backpic"></div>
    <div class="title">
      <h1>{{firstscreen.name}}</h1>
      <h2>{{firstscreen.msg}}</h2>
    </div>
  </div>
  <div id="nextscreen" class="nextscreen">
    <div id="ntbackpic" class="nextbackpic blur-mask"></div>
    <div class="avatar" id="avatar">
      <img src="../assets/avatar.jpg" style="border-radius: 50%;max-width: 100px;max-height: 100px;vertical-align:middle;">
      <span style="display:inline-block;vertical-align: middle;"><h1 style="margin:0px;padding:0px;">{{firstscreen.name}}</h1><h3 style="margin:0px;padding:0px;">{{nextscreen.des}}</h3></span>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'screen',
  data () {
    return {
      firstscreen: {
        name: '186526',
        msg: 'Welcome'
      },
      nextscreen: {
        des: '啥都不会的屑'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.firstscreen{
  overflow: hidden;
}
.nextbackpic{
  overflow: hidden;
  position: relative;
}
.nextscreen .nextbackpic{
  width: 100%;
  height: 100vh;
}
.firstscreen .backpic{
  width: 100%;
  height: 100vh;
}
.backpic{
  background: url("../assets/mask.jpg") center center / cover no-repeat;
  transform: translate3d(0px, 0px, 0px);
}
h1, h2 {
  font-weight: normal;
}
h1 {
  font-size: 48px;
}
h2 {
  font-size: 28px;
}
.title{
  color: #d7d7d7;
  position: absolute;
  top: 38%;
  left: 5%;
}
.fadein{
  opacity: 1;
  transition: all .4s;
}
.fadeoutframe{
  opacity: 0;
}
.fadeinframe{
  opacity: 1;
}
.blur-mask{
  filter: blur(2px);
  transition: all .5s;
}
.avatar{
  position: absolute;
  top:110%;
  left:5%;
}
.nextbackpic {
  display: flex;
  background: url("https://api.ixiaowai.cn/gqapi/gqapi.php") center center / cover no-repeat;
  transform: translate3d(0px, 0px, 0px);
}
.more-blur{
  filter: blur(15px);
  transition: all 3s;
}
.menhera{
  max-width: 100px;
  max-height: 100px;
}
@media screen and (max-width: 768px){
  .title{
    text-align: center;
    left: 0%;
    right: 0%;
  }
}
</style>



